import { PolicyService } from './../Services/policy.service';
import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { FileUpload } from '../Models/file.model';
import { Chart } from 'chart.js'
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFirestore } from '@angular/fire/firestore';
import { NgxQrcodeElementTypes, NgxQrcodeErrorCorrectionLevels } from '@techiediaries/ngx-qrcode';
import { FullCalendarComponent } from '@fullcalendar/angular';
import { CalendarOptions, EventInput } from '@fullcalendar/core';
import { EventSettingsModel, View } from '@syncfusion/ej2-angular-schedule';

@Component({
  selector: 'app-details-upload',
  templateUrl: './details-upload.component.html',
  styleUrls: ['./details-upload.component.scss']
})
export class DetailsUploadComponent implements OnInit {
  @ViewChild('calendar') calendarComponent: FullCalendarComponent; // the #calendar in the template

  calendarVisible = true;

  calendarWeekends = true;
  policies = []


  calendarEvents: EventInput[] = [
    { title: 'Event Now', start: new Date() }
  ];

  //QR code
  elementType = NgxQrcodeElementTypes.URL;
  correctionLevel = NgxQrcodeErrorCorrectionLevels.HIGH;
  value = 'https://www.techiediaries.com/';
  ///
  @Input() fileUpload: FileUpload;
  Linechart = [];
  Data =[]
  DataLabels=[]
  constructor(private uploadService: PolicyService,private firestore: AngularFirestore,
    private db:AngularFireDatabase) { }

  ngOnInit(): void {




  }
  calendarOptions: CalendarOptions = {
    initialView: 'dayGridMonth',
    weekends: false,
    dateClick: this.handleDateClick.bind(this), // bind is important!
    initialEvents:
     [//this.array
     { title: 'event 1', date: '2020-10-01'},
      { title: 'event 2', date: '2020-10-05' },
    ]

    //let title = data["Chart"].map((c)=>c.)
  }


  public handleAddressChange(address) {
  // Do some stuff
}
  deleteFileUpload(fileUpload) {
    //this.uploadService.deleteFileUpload(fileUpload);
  }
  random_rgba(){
    var o = Math.round,r =Math.random, s=255;
    return 'rgba('+o(r()*s)+','+o(r()*s)+','+o(r()*s)+',0.7)';
  }

  PolaraArea()
  {
    this.firestore.collection("Report ").snapshotChanges().subscribe(items=>{
      this.Data =[];
      this.DataLabels =[]
      items.forEach(a=>{

       let item:any = a.payload.doc.data()
       item.id = a.payload.doc.id;
       //console.log(item)
       this.Data.push(item.Quantity)
       this.DataLabels.push(item.ToolName)

      })



    this.Linechart.push(new Chart('canvass', {
      type: 'bar',
      data: {
        labels:   this.DataLabels,
        datasets: [
          {
            data: this.Data,
            borderColor: '#3cb371',
            backgroundColor: [
              this.random_rgba(),
              this.random_rgba(),
              this.random_rgba(),
            ],
          }
        ],
      },
      options: {
        legend: {
          display: false
        },
        scales: {
          xAxes: [{
            display: false,
            ticks: {  beginAtZero:true,min: 0 },
          }],
          yAxes: [{
            display: true,
            ticks: {  beginAtZero:true,min: 0 },
          },
        ],


        }
      }
    }));
  })
  }

  ///////////////////////////////
  toggleVisible() {
    this.calendarVisible = !this.calendarVisible;
  }

  toggleWeekends() {
    this.calendarWeekends = !this.calendarWeekends;
  }

  gotoPast() {
    let calendarApi = this.calendarComponent.getApi();
    calendarApi.gotoDate('2000-01-01'); // call a method on the Calendar object
  }

  handleDateClick(arg) {
    if (confirm('Would you like to add an event to ' + arg.dateStr + ' ?')) {
      this.calendarEvents = this.calendarEvents.concat({ // add new event data. must create new array
        title: 'New Event',
        start: arg.date,
        allDay: arg.allDay
      })
    }
  }

}
