@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
 //Maps
 latitude: number;
  longitude: number;
  zoom: number;
  address: string;
  private geoCoder;
 //search
  @ViewChild('searchbar',{static: true}) searchbar: ElementRef;
  searchText = '';
  progress: { percentage: number } = { percentage: 0 };

  //xcel import
  importContacts: Contact[] = [];

  toggleSearch: boolean = false;
  title = 'angular-firebase-crud';
  addressFormGroup: FormGroup; //location things
  @ViewChild("placesRef") placesRef : GooglePlaceDirective;

  Policy:AngularFireObject<any>
  policies =[];
  ActivateUpdate =false;
  currentFileUpload: FileUpload;
  selectedFiles: FileList;
  searchElementRef: any;

  openSearch() {
    this.toggleSearch = true;
    this.searchbar.nativeElement.focus();
  }



  constructor(public policyService: PolicyService,
    private firestore: AngularFirestore,
    private db:AngularFireDatabase,private AfAuth:AngularFireAuth,  private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone) { }

    fileUploads: any[];

  ngOnInit():void {
//MAps
 //load Places Autocomplete
 this.mapsAPILoader.load().then(() => {
  this.setCurrentLocation();
  this.geoCoder = new google.maps.Geocoder;

  let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement);
  autocomplete.addListener("place_changed", () => {
    this.ngZone.run(() => {
      //get the place result
      let place: google.maps.places.PlaceResult = autocomplete.getPlace();

      //verify result
      if (place.geometry === undefined || place.geometry === null) {
        return;
      }

      //set latitude, longitude and zoom
      this.latitude = place.geometry.location.lat();
      this.longitude = place.geometry.location.lng();
      this.zoom = 12;
    });
  });
});


    //import



    this.addressFormGroup = new FormGroup({
      address: new FormControl(),
    });
    this.addressFormGroup.get('address').valueChanges.subscribe(value => console.log('value changed', value))

    this.firestore.collection("coffeeOrders").snapshotChanges().subscribe(items=>{
      this.policies =[];
      items.forEach(a=>{

       let item:any = a.payload.doc.data()
       item.id = a.payload.doc.id;
       this.policies.push(item)
      })
      console.log(this.policies)
    })
    //location
    this.policyService.getFileUploads(6).snapshotChanges().pipe(map(changes=>changes.map(c=>({key:c.payload, ...c.payload.val()})))).subscribe(fileUploads=>{
      this.fileUploads = fileUploads
    })
     ;

     console.log(this.fileUploads)



  }

 /* create(policy: Policy){
      this.policyService.createPolicy(policy);
  }*/

  update(id) {
    console.log(id)
    this.Policy = this.db.object('coffeeOrders/' + id);



    this.firestore.doc(`coffeeOrders/${id}`).snapshotChanges().subscribe(items=>{
      let item:any = items.payload.data()
      console.log(item)
     this.policyService.form.setValue(item)
    })
  }





  //location
  public handleAddressChange(address) {
    // Do some stuff
    console.log(address)
}
  Delete(item) {
    this.firestore.doc(`coffeeOrders/${item.id}`).delete()
  }


  onSubmit()
  {

    let data = this.policyService.form.value;
    console.log(data)

   this.policyService.createCoffeeOrder(data).then(res => {
          console.log("In")
          this.thisToast()
          this.policyService.form.reset();

       });
  }
 thisToast(){

 }

 //uploading
 selectFile(event) {
  this.selectedFiles = event.target.files;
}
 upload()
   {
    const file = this.selectedFiles.item(0);
    this.selectedFiles = undefined;
    //console.log(file.type)
    this.currentFileUpload = new FileUpload(file);
    this.policyService.pushFileToStorage(this.currentFileUpload,this.progress)
   }


    Download1()
    {
const options = {
          margin: [1,0],
          filename: 'report.pdf',
          image:{type: 'jpeg',quality:'0.98'},
          html2canvas:{ scale: 2 },
          jsPDF:{orientation:'portrait'}
        };
          //console.log("here1")
        const content: Element = document.getElementById('content');
        console.log("heree",content)
          //console.log(options)
        html2pdf().from(content).set(options).save();
    }

    ////

    public Download() {
      const data = document.getElementById('content');

      html2canvas(data).then(canvas => {
        // Few necessary setting options
        console.log(canvas)
        const imgWidth = 100;
        const pageHeight = 400;
        const imgHeight = canvas.height * imgWidth / canvas.width;
        const heightLeft = imgHeight;
        const contentDataURL = canvas.toDataURL('image/png');
        const pdf = new jsPDF() // A4 size page of PDF
        const position = 0;
        pdf.addImage(contentDataURL, 'PNG', 25, position, imgWidth, imgHeight);
        pdf.save('testPdf.pdf'); // Generated PDF
      });


    }


    ///exporting
    onFileChange(evt: any) {
      const target: DataTransfer = <DataTransfer>(evt.target);
      if (target.files.length !== 1) throw new Error('Cannot use multiple files');

      const reader: FileReader = new FileReader();
      reader.onload = (e: any) => {

        const bstr: string = e.target.result;
        const data = <any[]>this.policyService.importFromFile(bstr);

        const header: string[] = Object.getOwnPropertyNames(new Contact());
        const importedData = data.slice(1,-1);
        console.log(importedData)
        this.importContacts = importedData.map(arr => {
          const obj = {};
          for (let i = 0; i < header.length; i++) {
            const k = header[i];
            obj[k] = arr[i];

          }



          return <Contact>obj;
        })

      };
      reader.readAsBinaryString(target.files[0]);

    }

    show()
    {
      for (let item of this.importContacts) {
        console.log(item?.name +" "+ item?.email+""+item?.phone)
    }
  }



    //QR code
    elementType = NgxQrcodeElementTypes.URL;
    correctionLevel = NgxQrcodeErrorCorrectionLevels.HIGH;
    value = 'https://www.techiediaries.com/';


   // Get Current Location Coordinates
   private setCurrentLocation() {
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.latitude = position.coords.latitude;
        this.longitude = position.coords.longitude;
        this.zoom = 8;
        this.getAddress(this.latitude, this.longitude);
      });
    }
  }


  markerDragEnd($event:AGMMouseEvent) {
    console.log($event);
    this.latitude = $event.coords.lat;
    this.longitude = $event.coords.lng;
    this.getAddress(this.latitude, this.longitude);
  }

  getAddress(latitude, longitude) {
    this.geoCoder.geocode({ 'location': { lat: latitude, lng: longitude } }, (results, status) => {
      console.log(results);
      console.log(status);
      if (status === 'OK') {
        if (results[0]) {
          this.zoom = 12;
          this.address = results[0].formatted_address;
        } else {
          window.alert('No results found');
        }
      } else {
      //  window.alert('Geocoder failed due to: ' + status);
      }

    });
  }
}
