import { AfterViewInit, Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { EventInput } from '@fullcalendar/core';
import { EventSettingsModel, PopupEventArgs, View } from '@syncfusion/ej2-angular-schedule';
import * as firebase from "firebase/app";
import "firebase/firestore"
@Component({
  selector: 'app-calender',
  templateUrl: './calender.component.html',
  styleUrls: ['./calender.component.scss']
})
export class CalenderComponent implements OnInit,AfterViewInit {
  calendarVisible = true;

  public currentView: View = 'Month';
  calendarWeekends = true;
  policies = []
  public selectedDate: Date = new Date(2020, 1, 15);
  public data: object[] = [{
    Id: 2,
    Subject: 'Paris',
    StartTime: new Date(2020, 1, 15, 10, 0),
    EndTime: new Date(2020, 1, 15, 12, 30)
}];

public eventSettings: EventSettingsModel = {
    dataSource: this.data
};

  constructor(public firestore: AngularFirestore,) {
    let i=0
    this.firestore
    .collection<any>("coffeeOrders")
    .valueChanges()
    .subscribe((data) => {
     let  rray = []
      data.forEach((element) => {
        let dates= {
          Subject: 'Vacation',
          StartTime: new Date(element.creationDate),
          EndTime: new Date(element.effectiveDate),
          IsAllDay: false
         }
         this.policies.push(dates)

      });
      this.eventSettings =  {
        dataSource: this.policies
      };



  })

   }
   ngAfterViewInit(){

}


  ngOnInit(): void {

  }

  popupOpen(e:PopupEventArgs)
  {
    console.log('this is are event', e)
  }

}
