import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { Policy } from '../Models/policy.model';
import { FormControl, FormGroup } from "@angular/forms";
import { AngularFireDatabase ,AngularFireList } from '@angular/fire/database';
import * as firebase from 'firebase/app';
import { FileUpload } from '../Models/file.model';
import 'firebase/storage';

//xcel
import * as XLSX from 'xlsx';
@Injectable({
  providedIn: 'root'
})
export class PolicyService {

  form = new FormGroup({
    policyNumber: new FormControl(''),
    creationDate: new FormControl(''),
    effectiveDate: new FormControl(''),
    expireDate: new FormControl(''),
    paymentOption: new FormControl(''),
    policyAmount: new FormControl(''),
    extraInfo: new FormControl('')
  })
  private basePath = '/uploads';
  createCoffeeOrder(data) {
    return new Promise<any>((resolve, reject) =>{
        this.firestore
            .collection("coffeeOrders")
            .add(data)
            ;
    });
  }
  constructor(private firestore: AngularFirestore,private db: AngularFireDatabase) { }
  getPolicies() {
    return this.firestore.collection('policies').snapshotChanges();
  }
  createPolicy(policy: Policy){
    return this.firestore.collection('policies').add(policy);
  }
  pdatePolicy(policy: Policy){
    delete policy.id;
    this.firestore.doc('policies/' + policy.id).update(policy);
  }
  deletePolicy(policyId: string){
    this.firestore.doc('policies/' + policyId).delete();
  }

  //uploading

  pushFileToStorage(fileUpload: FileUpload,progress: { percentage: number }) {
    const storageRef = firebase.storage().ref();
    const uploadTask = storageRef.child(`${this.basePath}/${fileUpload.file.name}`).put(fileUpload.file);
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,   (snapshot) => {
      // in progress
      const snap = snapshot as firebase.storage.UploadTaskSnapshot;
      progress.percentage = Math.round((snap.bytesTransferred / snap.totalBytes) * 100);
    },
      (error) =>{
        console.log(error)
      },
      ()=>{
        //success
        uploadTask.snapshot.ref.getDownloadURL().then(downloadURL => {
          console.log('File available at', downloadURL);
          fileUpload.url = downloadURL;
          fileUpload.name = fileUpload.file.name;
          this.saveFileData(fileUpload);
        });
      }

    )
  }
  private saveFileData(fileUpload: FileUpload) {
    this.db.list(`${this.basePath}/`).push(fileUpload);
  }


  getFileUploads(numberItems): AngularFireList<FileUpload> {
    return this.db.list(this.basePath, ref =>
      ref.limitToLast(numberItems));
  }

  ///xcel import
  public importFromFile(bstr: string): XLSX.AOA2SheetOpts {
    /* read workbook */
    const wb: XLSX.WorkBook = XLSX.read(bstr, { type: 'binary' });

    /* grab first sheet */
    const wsname: string = wb.SheetNames[0];
    const ws: XLSX.WorkSheet = wb.Sheets[wsname];

    /* save data */
    const data = <XLSX.AOA2SheetOpts>(XLSX.utils.sheet_to_json(ws, { header: 1 }));

    return data;
  }

}
